<template>
  <div>
    <p class="text-4xl text-MyColor-H3 font-bold pt-4">操作符</p>
    <p class="text-3xl text-MyColor-H5">運算符</p>
    <br />
    <table class="mx-auto my-2 w-4/5">
      <thead>
        <tr>
          <th class="w-1/2 px-4 py-2">項目</th>
          <th class="w-1/4 px-4 py-2">VB</th>
          <th class="w-1/4 px-4 py-2">VB範例</th>
          <th class="w-1/4 px-4 py-2">PyThon</th>
          <th class="w-1/4 px-4 py-2">PyThon範例</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="border px-4 py-2">開次方</td>
          <td class="border px-4 py-2">^</td>
          <td class="border px-4 py-2">3 ^ 2 (9)</td>
          <td class="border px-4 py-2">**</td>
          <td class="border px-4 py-2">4 ** 0.5 (2)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">乘</td>
          <td class="border px-4 py-2">*</td>
          <td class="border px-4 py-2">3 * 2 (6)</td>
          <td class="border px-4 py-2">*</td>
          <td class="border px-4 py-2">4 * 2 (8)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">除</td>
          <td class="border px-4 py-2">/</td>
          <td class="border px-4 py-2">2 / 3 (1.666...)</td>
          <td class="border px-4 py-2">/</td>
          <td class="border px-4 py-2">4 / 2 (2)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">整數除法(即捨去小數點)</td>
          <td class="border px-4 py-2">\</td>
          <td class="border px-4 py-2">2 \ 3 (1)</td>
          <td class="border px-4 py-2">//</td>
          <td class="border px-4 py-2">2 // 7 (0)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">取餘數</td>
          <td class="border px-4 py-2">Mod</td>
          <td class="border px-4 py-2">0 Mod 2 (0)</td>
          <td class="border px-4 py-2">%</td>
          <td class="border px-4 py-2">10 % 3 (1)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">加</td>
          <td class="border px-4 py-2">+</td>
          <td class="border px-4 py-2">2 + 3 (5)</td>
          <td class="border px-4 py-2">+</td>
          <td class="border px-4 py-2">1 + 4 (5)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">減</td>
          <td class="border px-4 py-2">-</td>
          <td class="border px-4 py-2">2 - 3 (-1)</td>
          <td class="border px-4 py-2">-</td>
          <td class="border px-4 py-2">10 - 3 (7)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">字串合併</td>
          <td class="border px-4 py-2">&</td>
          <td class="border px-4 py-2">2 & 3 ("23")</td>
          <td class="border px-4 py-2">PyThon沒有</td>
          <td class="border px-4 py-2">PyThon沒有</td>
        </tr>
      </tbody>
    </table>
    <br />
    <p class="text-justify Indent">口訣</p>
    <ol class="text-justify list-decimal list-inside Indent">
      <li>先次方 再乘除餘 然後 加減</li>
      <li>
        因為手舉起來通常都是朝內所以 左手舉起來是除法 右手舉起來是整數除法
      </li>
      <li>字串合併(&)會把資料型態轉成字串在相加</li>
    </ol>
    <br />
    <p class="text-3xl text-MyColor-H5">比較符</p>
    <br />
    <table class="mx-auto my-2 w-4/5">
      <thead>
        <tr>
          <th class="w-1/2 px-4 py-2">項目</th>
          <th class="w-1/4 px-4 py-2">VB</th>
          <th class="w-1/4 px-4 py-2">VB範例</th>
          <th class="w-1/4 px-4 py-2">PyThon</th>
          <th class="w-1/4 px-4 py-2">PyThon範例</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="border px-4 py-2">等於</td>
          <td class="border px-4 py-2">=</td>
          <td class="border px-4 py-2">2 = 2 (True)</td>
          <td class="border px-4 py-2">==</td>
          <td class="border px-4 py-2">3 == 2 (False)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">不等於</td>
          <td class="border px-4 py-2">&#x003c;&#x003e;</td>
          <td class="border px-4 py-2">3 &#x003c;&#x003e; 2 (True)</td>
          <td class="border px-4 py-2">!=</td>
          <td class="border px-4 py-2">4 != 2 (True)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">大於</td>
          <td class="border px-4 py-2">&#x003e;</td>
          <td class="border px-4 py-2">2 &#x003e; 3 (False)</td>
          <td class="border px-4 py-2">&#x003e;</td>
          <td class="border px-4 py-2">4 &#x003e; 2 (True)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">小於</td>
          <td class="border px-4 py-2">&#x003c;</td>
          <td class="border px-4 py-2">2 &#x003c; 2 (False)</td>
          <td class="border px-4 py-2">&#x003c;</td>
          <td class="border px-4 py-2">2 &#x003c; 7 (True)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">大於等於</td>
          <td class="border px-4 py-2">&#x003e;=</td>
          <td class="border px-4 py-2">2 &#x003e;= 2 (True)</td>
          <td class="border px-4 py-2">&#x003e;=</td>
          <td class="border px-4 py-2">4 &#x003e;= 2 (True)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">小於等於</td>
          <td class="border px-4 py-2">&#x003c;=</td>
          <td class="border px-4 py-2">2 &#x003c;= 1 (False)</td>
          <td class="border px-4 py-2">&#x003c;=</td>
          <td class="border px-4 py-2">2 &#x003c;= 7 (True)</td>
        </tr>
      </tbody>
    </table>
    <br />
    <p class="text-justify Indent">口訣</p>
    <ol class="text-justify list-decimal list-inside Indent">
      <li>!有警告的意思，即Not、禁止...的感覺</li>
      <li>VB不等於記菱形圖案</li>
      <li>PyThon一個=不是等於，兩個=才是等於，一個=叫做賦值</li>
      <li>必較之後必然會產生Bool值</li>
    </ol>
    <br />
    <p class="text-3xl text-MyColor-H5">邏輯符</p>
    <br />
    <table class="mx-auto my-2 w-4/5">
      <thead>
        <tr>
          <th class="w-1/2 px-4 py-2">項目</th>
          <th class="w-1/4 px-4 py-2">VB</th>
          <th class="w-1/4 px-4 py-2">VB範例</th>
          <th class="w-1/4 px-4 py-2">PyThon</th>
          <th class="w-1/4 px-4 py-2">PyThon範例</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="border px-4 py-2">反</td>
          <td class="border px-4 py-2">Not</td>
          <td class="border px-4 py-2">Not False (True)</td>
          <td class="border px-4 py-2">not</td>
          <td class="border px-4 py-2">not True (False)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">及(兩邊都是True)[也有人稱且]</td>
          <td class="border px-4 py-2">And</td>
          <td class="border px-4 py-2">True And False (False)</td>
          <td class="border px-4 py-2">and</td>
          <td class="border px-4 py-2">True and True (True)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">或(兩邊任一邊為True)</td>
          <td class="border px-4 py-2">Or</td>
          <td class="border px-4 py-2">True Or False (True)</td>
          <td class="border px-4 py-2">or</td>
          <td class="border px-4 py-2">False or False (False)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">互斥或(一邊True 一邊False)</td>
          <td class="border px-4 py-2">Xor</td>
          <td class="border px-4 py-2">
            True Xor False (True)
          </td>
          <td class="border px-4 py-2">True Xor True (False)</td>
          <td class="border px-4 py-2">PyThon沒有互斥或</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">
            在(左邊是元素右邊是迭代物件，表示元素有在迭代物件裡)
          </td>
          <td class="border px-4 py-2">VB沒有</td>
          <td class="border px-4 py-2">
            有類似的Function
          </td>
          <td class="border px-4 py-2">in</td>
          <td class="border px-4 py-2">"a" in "apple"(True)</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">
            不在(同上只是沒有在迭代物件裡)
          </td>
          <td class="border px-4 py-2">VB沒有</td>
          <td class="border px-4 py-2">
            有類似的Function
          </td>
          <td class="border px-4 py-2">not in</td>
          <td class="border px-4 py-2">3 not in (3, 4, 5) (False)</td>
        </tr>
      </tbody>
    </table>
    <br />
    <p class="text-3xl text-MyColor-H5">賦值符</p>
    <br />
    <table class="mx-auto my-2 w-4/5">
      <thead>
        <tr>
          <th class="w-1/2 px-4 py-2">項目</th>
          <th class="w-1/4 px-4 py-2">VB</th>
          <th class="w-1/4 px-4 py-2">VB範例</th>
          <th class="w-1/4 px-4 py-2">PyThon</th>
          <th class="w-1/4 px-4 py-2">PyThon範例</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td class="border px-4 py-2">賦值</td>
          <td class="border px-4 py-2">=</td>
          <td class="border px-4 py-2">Dim a = 3</td>
          <td class="border px-4 py-2">=</td>
          <td class="border px-4 py-2">a = 2</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值次方</td>
          <td class="border px-4 py-2">^=</td>
          <td class="border px-4 py-2">a ^= 2</td>
          <td class="border px-4 py-2">**=</td>
          <td class="border px-4 py-2">a **= 0.5</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值乘</td>
          <td class="border px-4 py-2">*=</td>
          <td class="border px-4 py-2">a *= 5</td>
          <td class="border px-4 py-2">*=</td>
          <td class="border px-4 py-2">a *= 2</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值除</td>
          <td class="border px-4 py-2">/=</td>
          <td class="border px-4 py-2">a /= 2</td>
          <td class="border px-4 py-2">/=</td>
          <td class="border px-4 py-2">a /= 10</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值整數除</td>
          <td class="border px-4 py-2">\=</td>
          <td class="border px-4 py-2">a \= 2</td>
          <td class="border px-4 py-2">//=</td>
          <td class="border px-4 py-2">a //= 10</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值餘</td>
          <td class="border px-4 py-2">VB沒有</td>
          <td class="border px-4 py-2">VB沒有</td>
          <td class="border px-4 py-2">%=</td>
          <td class="border px-4 py-2">a %= 3</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值加</td>
          <td class="border px-4 py-2">+=</td>
          <td class="border px-4 py-2">a += 3</td>
          <td class="border px-4 py-2">+</td>
          <td class="border px-4 py-2">a += 4</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值減</td>
          <td class="border px-4 py-2">-=</td>
          <td class="border px-4 py-2">a -= 3</td>
          <td class="border px-4 py-2">-=</td>
          <td class="border px-4 py-2">a -= 3</td>
        </tr>
        <tr>
          <td class="border px-4 py-2">賦值字串合併</td>
          <td class="border px-4 py-2">&=</td>
          <td class="border px-4 py-2">a &= 10</td>
          <td class="border px-4 py-2">PyThon沒有</td>
          <td class="border px-4 py-2">PyThon沒有</td>
        </tr>
      </tbody>
    </table>
    <br />
    <p class="text-justify Indent">口訣</p>
    <ol class="text-justify list-decimal list-inside Indent">
      <li>左邊必須是變數，只有賦值可以配宣告即 Dim a = 10，不行Dim a += 10</li>
      <li>要使用賦值次方、乘等等必須先宣告即</li>
      <li><code>Dim a = 10</code></li>
      <li><code>a *= 10</code></li>
      <li>a會是100</li>
      <li>先右再左，即先計算右邊在丟給左邊變數</li>
    </ol>
    <br />
    <p class="text-left underline Indent">陷阱1</p>
    <ol class="text-left list-none list-inside Indent">
      <li>VB ,PyThon通用</li>
      <li><code>Dim A = 10</code></li>
      <li><code>A *= 10 + 2</code></li>
      <li>轉換後是A = A * (10 + 2)</li>
      <li>不是A = A * 10 + 2</li>
      <li>基於先右邊再左邊原則</li>
      <li>結果為120 (10 * (10 + 2))</li>
    </ol>
    <br />
    <p class="text-left underline Indent">陷阱2</p>
    <ol class="text-left list-none list-inside Indent">
      <li>VB</li>
      <li><code>Dim A As Integer = 10</code></li>
      <li><code>A /= 4</code></li>
      <li>轉換後是A = 10 /4</li>
      <li>基於先右邊再左邊原則</li>
      <li>變成 A = 2.5</li>
      <li>再基於上一個單元教的弱型別轉換是跑[ Cint ]函式即4捨6入5取偶</li>
      <li>A 會是 2</li>
    </ol>
    <br />
    <p class="text-left underline Indent">陷阱3</p>
    <ol class="text-left list-none list-inside Indent">
      <li>VB</li>
      <li><code>B = (A = 10)</code></li>
      <li>VB一行只會有一個賦值，或是賦值乘等等...</li>
      <li>所以()內的=是等於，()外的=是賦值</li>
      <li>假設A是8</li>
      <li>B = False</li>
      <li>B 會是 False</li>
    </ol>
    <br />
    <p class="text-left underline Indent">陷阱4</p>
    <ol class="text-left list-none list-inside Indent">
      <li>PyThon</li>
      <li><code>A = 10</code></li>
      <li><code>A /= 3</code></li>
      <li>轉換後是A = 10 / 3</li>
      <li>基於先右邊再左邊原則</li>
      <li>變成 A = 3.333333...</li>
      <li>再基於上一個單元教的動態型別特色，所以A從int Type變成float Type</li>
      <li>A 會是 3.333333...</li>
    </ol>
    <br />
    <p class="text-left underline Indent">陷阱5</p>
    <ol class="text-left list-none list-inside Indent">
      <li>PyThon可以但VB不能嚴格強調!!</li>
      <li><code>A = B = C = 20</code></li>
      <li>基於先右邊再左邊原則</li>
      <li>C先賦值20</li>
      <li>B再賦值C(20)</li>
      <li>依此類推</li>
      <li>所以A和B和C都是20</li>
    </ol>
    <br />
    <p class="text-left underline Indent">先運算 再比較 然後邏輯 再給值</p>
    <br />
    <br />
  </div>
</template>
<script>
export default {
  name: "Signs"
};
</script>
